"use client";

import React, { useState } from "react";
import { useRouter } from "next/navigation";
import { addExpense, CATEGORIES } from "../../lib/expenses";
import { Button } from "../../components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "../../components/ui/card";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "../../components/ui/select";
import { Input } from "../../components/ui/input";
import { Label } from "../../components/ui/label";
import { Textarea } from "../../components/ui/textarea";
import IncomeAllocationModal from "../../components/IncomeAllocationModal";

export default function AddEntryPage() {
  const router = useRouter();
  const [amount, setAmount] = useState("");
  const [type, setType] = useState<"chi" | "thu">("chi");
  const [category, setCategory] = useState("");
  const [description, setDescription] = useState("");
  const [isSubmitting, setIsSubmitting] = useState(false);

  // Calculator functions
  const appendToAmount = (value: string) => {
    if (value === "." && amount.includes(".")) return;
    setAmount(prev => prev + value);
  };

  const clearAmount = () => {
    setAmount("");
  };

  const deleteLastDigit = () => {
    setAmount(prev => prev.slice(0, -1));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!amount || !category) {
      alert("Vui lòng nhập đầy đủ thông tin!");
      return;
    }

    const numAmount = parseFloat(amount);
    if (isNaN(numAmount) || numAmount <= 0) {
      alert("Số tiền không hợp lệ!");
      return;
    }

    setIsSubmitting(true);
    
    try {
      addExpense({
        amount: numAmount,
        type,
        category,
        description: description.trim() || undefined,
      });
      
      router.push("/");
    } catch (error: any) {
      alert("Có lỗi khi lưu dữ liệu: " + error.message);
    } finally {
      setIsSubmitting(false);
    }
  };

  const calculatorButtons = [
    ["7", "8", "9"],
    ["4", "5", "6"],
    ["1", "2", "3"],
    ["0", ".", "⌫"]
  ];

  return (
    <div className="max-w-md mx-auto space-y-6">
      <div className="text-center">
        <h1 className="text-2xl font-bold mb-2">Ghi chi tiêu</h1>
        <p className="text-muted-foreground">Nhập thông tin giao dịch</p>
      </div>

      <form onSubmit={handleSubmit} className="space-y-6">
        {/* Amount Display */}
        <Card>
          <CardHeader>
            <CardTitle className="text-center">Số tiền</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-center">
              <div className="text-3xl font-bold mb-2 min-h-[3rem] flex items-center justify-center border-2 border-dashed border-border rounded-lg p-4">
                {amount || "0"} VND
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Calculator */}
        <Card>
          <CardContent className="pt-6">
            <div className="grid grid-cols-3 gap-3">
              {calculatorButtons.flat().map((btn, index) => (
                <Button
                  key={index}
                  type="button"
                  variant="outline"
                  className="h-12 text-lg font-semibold"
                  onClick={() => {
                    if (btn === "⌫") {
                      deleteLastDigit();
                    } else {
                      appendToAmount(btn);
                    }
                  }}
                >
                  {btn}
                </Button>
              ))}
            </div>
            <div className="grid grid-cols-2 gap-3 mt-3">
              <Button
                type="button"
                variant="destructive"
                onClick={clearAmount}
                className="h-12"
              >
                Xóa hết
              </Button>
              <Button
                type="button"
                variant="secondary"
                onClick={() => appendToAmount("000")}
                className="h-12"
              >
                000
              </Button>
            </div>
          </CardContent>
        </Card>

        {/* Transaction Type */}
        <div className="space-y-2">
          <Label>Loại giao dịch</Label>
          <div className="grid grid-cols-2 gap-3">
            <Button
              type="button"
              variant={type === "chi" ? "default" : "outline"}
              onClick={() => {
                setType("chi");
                setCategory("");
              }}
              className="h-12"
            >
              Chi tiêu
            </Button>
            <Button
              type="button"
              variant={type === "thu" ? "default" : "outline"}
              onClick={() => {
                setType("thu");
                setCategory("");
              }}
              className="h-12"
            >
              Thu nhập
            </Button>
          </div>
        </div>

        {/* Category */}
        <div className="space-y-2">
          <Label>Danh mục</Label>
          <Select value={category} onValueChange={setCategory}>
            <SelectTrigger className="h-12">
              <SelectValue placeholder="Chọn danh mục" />
            </SelectTrigger>
            <SelectContent>
              {CATEGORIES[type].map((cat) => (
                <SelectItem key={cat} value={cat}>
                  {cat}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>

        {/* Description */}
        <div className="space-y-2">
          <Label>Mô tả (tùy chọn)</Label>
          <Textarea
            value={description}
            onChange={(e) => setDescription(e.target.value)}
            placeholder="Ví dụ: Ăn trưa tại quán cơm..."
            className="min-h-[80px]"
          />
        </div>

        {/* Action Buttons */}
        <div className="grid grid-cols-2 gap-3">
          <Button
            type="button"
            variant="outline"
            onClick={() => router.push("/")}
            className="h-12"
            disabled={isSubmitting}
          >
            Hủy bỏ
          </Button>
          <Button
            type="submit"
            className="h-12"
            disabled={isSubmitting || !amount || !category}
          >
            {isSubmitting ? "Đang lưu..." : "Lưu"}
          </Button>
        </div>
      </form>

      {/* Quick Amount Buttons */}
      <Card>
        <CardHeader>
          <CardTitle className="text-sm">Số tiền thường dùng</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-3 gap-2">
            {["10000", "20000", "50000", "100000", "200000", "500000"].map((quickAmount) => (
              <Button
                key={quickAmount}
                type="button"
                variant="outline"
                size="sm"
                onClick={() => setAmount(quickAmount)}
              >
                {parseInt(quickAmount).toLocaleString("vi-VN")}
              </Button>
            ))}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
